@font-face {font-family: normal; src: url('./normal.ttf')}
@font-face {font-family: faster; src: url('./faster.ttf')}
@font-face {font-family: kalimba; src: url('./kalimba.otf')}

.clb {
    border: solid #000 1px;  
    border-radius: 8px;
    -webkit-box-shadow: 2px 2px 10px #000;
            box-shadow: 2px 2px 10px #000;
    min-width: 350px;
    max-width: 350px;
    min-height: 55px;
    overflow: hidden;
    background-image: url('pixelBackground.jpg');
    background-repeat: no-repeat;
    background-size: 100%;
    -webkit-transition: height 2000ms, opacity 4000ms;
    -o-transition: height 2000ms, opacity 4000ms;
    transition: height 2000ms, opacity 4000ms; 
    font-family: kalimba;
    font-size: 15px;
    /* letter-spacing: 1.2px; */

    text-shadow: 1px 1px 5px rgb(240, 240, 240);

}


.lb > .col-12 { overflow: hidden; -webkit-transition: height 2000ms, opacity 200ms; -o-transition: height 2000ms, opacity 200ms; transition: height 2000ms, opacity 200ms}

/* Hoebear */
.lb > .col-12:nth-child(1) { 
    /* border: solid #000 1px; */
    height: 0px;
    opacity: 0; 

} 

.lb > .col-12:nth-child(1) > .row > .col-7 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.lb > .col-12:nth-child(1) > .row > .col-7 > .row > .col-12 > img{
    
    position: relative;
    margin-right: 2px;
    top: -2px;
    left: 4px;
    width: 15px;
    -webkit-box-shadow: 1px 1px 1px rgb(90, 90, 90);
            box-shadow: 1px 1px 1px rgb(90, 90, 90);

}

 /* Levels */
.lb > .col-12:nth-child(2) { 
    height: 0;
    padding-top: 10px;
    opacity: 1; 
    overflow: hidden;
    background: rgba(0, 0, 0, 0.249);
    border-top: solid #000 1px;
    border-bottom: solid #000 1px;
    
}

.lb > .col-12:nth-child(2) > .row > .col-4 {
    
    height: 100px;
    text-align: center;

    padding: 0 1px;
    font-size: 15px;
    /* border: solid greenyellow 1px; */
    
}
.col-4 > .container > .row > .col-12  {
    padding: 0
}

.col-4 > .container > .row > .col-12 > img {
    width: 80%;
    padding: 0;
    margin: 0;
}
/* Stars */
.col-4 > .container > .row > .col-12 > div > i {
    font-size: 10px;
    color: #000;
}

