    border: solid #000 1px;
   
    background: rgba(0, 0, 0, 0.112);
    
} */




.leaderboard > .col-12 {
    padding: 0;
    height: 37px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
 
    /* border: solid red 1px; */
    
}

.leaderboard > button {
    border: solid #000 1px;
    border-radius: 0;
    padding: 0;
    position: absolute; 
    left: 35px;
    width: 300px;
    height: 35px;
  
    overflow: hidden;

    color: #000;
    
}

.leaderboard > button:nth-child(odd){
    background: #ccc;
}

.leaderboard > button:nth-child(even){
    background: rgb(155, 155, 155);
}

button > .row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    font-size: 15px;
  
    margin: 0;
    padding: 0;
    overflow: hidden;
}

button > .row > .col-5 {
    padding: 0;
    /* border: solid purple 1px; */
    text-align: left;
    overflow: hidden;
}

button > .row > .col-3 {
    padding: 0;
    text-align: center;
    overflow: hidden;
    /* border: solid green 1px; */
}

button > .row > .col-2 {
    padding: 0;
    padding-left: 10px;
    text-align: left;
    /* border: solid blue 1px; */
    position: relative;
    overflow: hidden;
}


button > .row > .col-2 > img {
    
    border-left: solid #000 2px;
    border-right: solid #000 2px;
    height: 33px; 

}


